import React, { Component } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
class ServiceTabExample extends Component{

    render(){

        const {isEng = false} = this.props;
        const titleHun = 'Szolgltatsaink';
        const titleEng = 'Our Services';
        
        /* service tab menu */
        let serviceTabMenuData = [
            {iconName: 'flaticon-024-statistics', tabMenuName: 'Tervezs'},
            {iconName: 'flaticon-028-manufacturing-plant', tabMenuName: 'Eszkz kereskedelem'},
            {iconName: 'flaticon-030-management', tabMenuName: 'Szaktancsads'},
            {iconName: 'flaticon-021-worker', tabMenuName: 'Szervz'},
        ];

        let serviceTabMenuDataEng = [
            {iconName: 'flaticon-024-statistics', tabMenuName: 'Project design'},
            {iconName: 'flaticon-028-manufacturing-plant', tabMenuName: 'Equipment sales'},
            {iconName: 'flaticon-030-management', tabMenuName: 'Product support'},
            {iconName: 'flaticon-021-worker', tabMenuName: 'After Sales Service'},
        ];

        let serviceTabMenuDatalist = (isEng ? serviceTabMenuDataEng : serviceTabMenuData).map((val, i)=>{
            return(
                <Tab key={i}>  
                    <span className="icon">
                        <i className={val.iconName} />
                    </span> 
                    <span className="text">{val.tabMenuName}</span></Tab>
            )
        });

        
        /* service tab content */
        
        let serviceTabContentData = [
            {
                bgUrl: "tervezes.jpg", 
                contentTitle: 'Tervezs', 
                contentDesc: 'A baromfi tartsi ignyeit maximlisan kiszolgl eszkzrendszer sszelltsa, tartstrben trtn elhelyezsnek s beptsnek megtervezse az llatjlti kvetelmnyek s krnyezetvdelmi szempontok figyelembevtelvel.', 
                serviceLink: 'planning'
            },
            {
                bgUrl: "kereskedelem.jpg", 
                contentTitle: 'Eszkz kereskedelem', 
                contentDesc: 'Trsasgunk vezet eurpai, valamint tengerentli gyrtk vilgsznvonal berendezseinek forgalmazsval, lehetv teszi a baromfitartk szmra a legjabb s kvetkez vtizedekre elremutat mszaki technolgia alkalmazst.' +
                    'Ennek rvn a termels fajlagos mutati javthatk, a tarts kzvetlen - takarmny, energia, munkaer stb. - kltsgei radiklisan cskkenthetk tovbb maximlisan biztosthatk a tarts llategszsggyi felttelei.', 
                serviceLink: 'sales'
            },
            {
                bgUrl: "szaktanacsadas.jpg", 
                contentTitle: 'Szaktancsads', 
                contentDesc: '24 ves eszkzforgalmazsi, beszerelsi s zemeltetsi tapasztalatunkat megbzink szolglatba lltva partnereink termelsi eredmnyei s piaci versenykpessge emelhet.', 
                serviceLink: 'support'
            },
            {
                bgUrl: "szerviz.jpg", 
                contentTitle: 'Szervz', 
                contentDesc: 'Raktrbzisunk s szakembereink a nap 24 rjban llnak rendelkezsre partnereink szmra telefonon vagy szemlyes konzultci keretben tartstechnolgiai s zemeltetsi krdsekben, szaktancsadsban, oktatsban. ', 
                serviceLink: 'service'
            },
        ];

        let serviceTabContentDataEng = [
            {
                bgUrl: "tervezes.jpg",
                contentTitle: 'Project design',
                contentDesc: '',
                serviceLink: 'planning'
            },
            {
                bgUrl: "kereskedelem.jpg",
                contentTitle: 'Equipment Sales',
                contentDesc: '',
                serviceLink: 'sales'
            },
            {
                bgUrl: "szaktanacsadas.jpg",
                contentTitle: 'Product support',
                contentDesc: '',
                serviceLink: 'support'
            },
            {
                bgUrl: "szerviz.jpg",
                contentTitle: 'After Sales Service',
                contentDesc: '',
                serviceLink: 'service'
            },
        ];

        const readMoreHun = 'Tovbbi rszletek';
        const readMoreEng = 'See more';

        let serviceTabContentDatalist = (isEng ? serviceTabContentDataEng : serviceTabContentData).map((val, i)=>{
            return(
                <TabPanel key={i}>
                    <div className="service-tab__single-content-wrapper" style={{ backgroundImage: `url(assets/img/service/${val.bgUrl})` }}>
                        <div className="service-tab__single-content">
                            <h3 className="service-tab__title">{val.contentTitle}</h3>
                            <p className="service-tab__text">{val.contentDesc}</p>
                            {!isEng && <a href={`${process.env.PUBLIC_URL}/${val.serviceLink}`} className="see-more-link">{readMoreHun}</a>}
                        </div>
                    </div>
                </TabPanel>
            )
        });

        return(
            <div id={'services'}>

                {/*====================  service tab area ====================*/}
                <div className="service-tab-area section-space--inner--60 section-space--inner--top--120">
                <div className="container">
                    <div className="row">
                    <div className="col-lg-12">
                        <div className="section-title-area text-center">
                        <h2 className="section-title section-space--bottom--50">{isEng ? titleEng : titleHun}</h2>
                        </div>
                    </div>
                    <div className="col-lg-12">
                        {/* service tab wrapper */}
                        
                        <div className="service-tab-wrapper">
                        <Tabs>
                            <div className="row no-gutters">
                                <div className="col-md-4">
                                    <TabList>
                                        <div className="service-tab__link-wrapper">
                                            {serviceTabMenuDatalist}
                                        </div>
                                    </TabList>
                                </div>

                                <div className="col-md-8">
                                    {serviceTabContentDatalist}
                                </div>
                            </div>
                        </Tabs>
                        </div>
                    </div>
                    </div>
                </div>
                </div>
                {/*====================  End of service tab area  ====================*/}
            </div>
        )
    }
}

export default ServiceTabExample;