import React, {Component} from 'react';
import NavBar from '../components/NavBar';
import BrandLogoSlider from '../components/BrandLogoSlider';
import Footer from '../components/Footer';
import MobileMenu from '../components/MobileMenu';
import PhotoGallery from './components/PhotoGallery';

class Hungerit extends Component{

    render(){
        return(
            <div>
                {/* Navigation bar */}
                <NavBar/>

                {/* breadcrumb */}
                {/*====================  breadcrumb area ====================*/}
                <div className="breadcrumb-area breadcrumb-bg">
                    <div className="container">
                        <div className="row">
                            <div className="col">
                                <div className="page-banner text-center">
                                    <h1>Projekt Ismertet</h1>
                                    <ul className="page-breadcrumb">
                                        <li><a href="/">kezdlap</a></li>
                                        <li><a href="projects">Projektjeink kzl</a></li>
                                        <li>Projekt Lers</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                {/*====================  End of breadcrumb area  ====================*/}

                {/*====================  project details page content ====================*/}
                <div className="page-wrapper section-space--inner--120">
                {/*Projects section start*/}
                <div className="project-section">
                    <div className="container">
                    <div className="row">
                        <div className="col-12 section-space--bottom--40">
                        <div className="project-image"><img src="assets/img/service/tiszakecske.jpg" className="img-fluid" alt="" /></div>
                        </div>
                        <div className="col-lg-4 col-12 section-space--bottom--30">
                            <div className="project-information">
                                <h3>Projekt Informci</h3>
                                <ul>
                                    <li><strong>Partner:</strong> <a href="project-details"> Hungerit Zrt.</a></li>
                                    <li><strong>Helyszn:</strong> Tiszakcske</li>
                                    <li><strong>Technolgia:</strong> Kacsa hizlal telep</li>
                                </ul>
                            </div>
                        </div>
                        <div className="col-lg-8 col-12 section-space--bottom--30 pl-30 pl-sm-15 pl-xs-15">
                            <div className="project-details">
                                <h2>Hungerit Zrt. Tiszakcske Kacsatelep</h2>
                                <p>A Hungerit Zrt. trsasgunkat bzta meg vgtermk kacsatart kapacitsnak nvelsre a zldmezs beruhzs keretben plt 21 istlls telep tartstechnolgiai berendezseinek sszelltsra s szlltsra.</p>
                                <p>Az el s utnevel tartsterekbe a lgkorszerbb kacsatart berendezsek kerltek beszerelsre mellyel tarts technolgiai ignyei a legmagasabb szinten elgthet ki a nevels s hizlals eredmnyessge pedig biztosthat.
                                </p>
                                <p>A 21 tartstrben komputer vezrls, automatizlt berendezsek vgzik az etetst s itatst, gondoskodnak az optimlis vilgtsrl, ftsrl, szellztetsrl. A tartstr klimatizcija elre bellthat a napszak s a mindenkori vszaknak megfelelen. </p>
                                <p>A nagymret telepen az zemeltetshez elengedhetetlen teli kommunikcit ugyancsak trsasgunk tervezte, szlltotta s irnytotta kiptst. </p>
                            </div>
                        </div>
                        <div className="col-12">
                            <PhotoGallery projectName={'tiszakecske'}/>
                        </div>
                    </div>
                    </div>
                </div>
                {/*Projects section end*/}

                </div>

                {/* Footer */}
                <Footer/>

                {/* Mobile Menu */}
                <MobileMenu/>

            </div>
        )
    }
}


export default Hungerit;